  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: lightblue;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  margin-top: 0;
  color: darkblue;
}

.App-link {
  color: #61dafb;
}

.MuiBox-root {
  margin-top: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.shiny-sections {
  display: inline-flex;
}

.shiny-section {
  background-color: #2e3440;
  padding: 5px;
  margin: 5px
}

.shiny-button {
  font-size: 30px;
  font-weight: normal;
  font-family: inherit;
  padding: 0.25em 0.5em;
  background-color: #fff;
  border: 2px solid var(--input-border);
  border-radius: 4px;
  cursor: pointer;
  font-family: inherit;
  margin-left: 10px;
}

.shiny-button:hover {
  background-color: #555555;
  color: white;
}

input {
  font-size: 30px;
  font-family: inherit;
  padding: 0.25em 0.5em;
  background-color: #fff;
  border: 2px solid var(--input-border);
  border-radius: 4px;
  margin-bottom: 10px;
}

#shiny-notification-panel {
  top: 0;
  bottom: unset;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 450px;
